import React, { Component, Fragment } from 'react'
import Head from 'next/head';
import '../static/css/style.scss'

export default class Layout extends Component {
    constructor(props) {
        super(props)

        this.state = {
            backTopShow: false
        }
    }

    componentWillMount() {
        window.addEventListener('scroll', this.handleScroll)
    }
    
    handleScrollTop() {
        window.scrollTo({
            left: 0,
            top: 0,
            behavior: 'smooth',
        });
    }
  
    render() {
        return (
            <Fragment>
                <Head>
                    <title>{this.props.title}</title>
                </Head>
                {this.props.children}
                <div id="back_top" onClick={this.handleScrollTop}><i className="iconfont icon-yooxi"></i></div>
            </Fragment>
        )
    }
}
